.incarnate_body {
    width: 100%;
    height: 100%;
    display: flex;
  
    font-size: 120%;
    font-family: Roboto, sans-serif;
    cursor: default;
}

.incarnate_details {
    width: 100%;
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
}

.incarnate_title {
    font-size: 350%;
    font-family: 'Playfair Display', serif;
}

.incarnate_excerpt {
    padding-left: 25%;
    padding-right: 25%;
    text-align: center;
    color: grey
}

.incarnate_selection {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-flow: wrap;
    justify-content: center;
}

.greySection {
    position: relative;
    margin-top: -200px;
    z-index: -100;
    width: 100%;
    height: 150px;
    background-color: #d6d6d6;
}

.greyDetailsSection {
    position: absolute;
    margin-top: -282px;
    z-index: -100;
    width: 100%;
    height: 320px;
    background-color: #d6d6d6;
}

.detailsSection {
    display: inline;
}

.incarnate_character {
    width: 137.8125px;
    height: 275px;
    margin: 7.5px;
    background-color: white;
    border-radius: 10px;
    border: 1px solid #d6d6d6;
}

.charaNames, .drachActive, .kaynActive, 
.kiyoneActive, .lirahActive, .misaActive,
.rookActive, .takuActive, .zerettaActive {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 10px;
    padding-right: 10px;
    font-size: 14px;
    margin-top: 0px;
    margin-bottom: 0px;
    height: 30px;
    color: white;
    border-top-left-radius: 7px;
    border-top-right-radius: 7px;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.charaNames {
    background-color: grey;
}

.incarnate_tagline {
    height: 42.5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 10px;
    padding-right: 10px;
    margin-top: 196px;

    color: grey;
    font-size: 14px;
    font-style: italic;
    line-height: 1;
    background-color: white;
    border-top: 1px solid black;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}

.card, 
.drachCardIcon, .drachCardInactive,
.kaynCardIcon, .kaynCardInactive,
.kiyoneCardIcon, .kiyoneCardInactive,
.lirahCardIcon, .lirahCardInactive,
.misaCardIcon, .misaCardInactive,
.rookCardIcon, .rookCardInactive,
.takuCardIcon, .takuCardInactive,
.zerettaCardIcon, .zerettaCardInactive {
    width: 137.8125px;
    height: 194.90625px;
    border-top: solid black 1px;
    border-bottom: solid black 1px;
    background: url('../components/incarnate_chara/incarnate_img/drach_fullbody.png') no-repeat;
    background-size: 137.8125px 194.90625px;
    -moz-transition: scaleX(-1);
    -webkit-transition: scaleX(-1);
    -o-transition: scaleX(-1);
    -ms-transition: scaleX(-1);
    transition: scaleX(-1);
}

.drachCardInactive, .kaynCardInactive,
.kiyoneCardInactive, .lirahCardInactive,
.misaCardInactive, .rookCardInactive,
.takuCardInactive, .zerettaCardInactive {
    opacity: 0.5;
    filter: grayscale(100%); 
}

/*DRACH -----------------------------------------------*/
.drachCardIcon, .drachCardInactive {
    background: url('../components/incarnate_chara/incarnate_img/drach_icon.png') no-repeat;
    background-size: 137.8125px 194.90625px;
    cursor: pointer;
}

.drachActive {
    background-color: #324a66;
    font-weight: bold;
}

