    height: 255px;
    border: 1px solid grey;
    border-radius: 5px;

    margin: 50px 5px -10px 0px;
}

.fullbodyImg {
    width: 454px;
    height: 650px;
    position: relative;
    float: right;

    margin: -100px 0px 0px 0px; 
    padding-top: 100px;
    z-index: 0;
}

.summary {
    display: inline-block; 
    text-align: left; 
    margin-left: 10px;
    margin-top: 50px;
    z-index: 2;
}

.summAtt {
    margin-top: -10px;
}

.top {
    width: 100%;
